  <script>
  import emailjs from 'emailjs-com';
//   import Footer from '@/components/footer'
//   import WelcomeSection from '@/components/welcome-section'
  import AboutSection from '@/components/about-section'
  import TeamSection from '@/components/team-section'
  import ModalVideo from '@/components/modal/video-modal.vue';
  import SocialMediaspButton from '@/components/social-media';

  export default {
    name: 'HomeView',
    data() {
        return {
            mailForm: {
                name: '',
                phone_number: '+7',
                question: '',
                message: '',
                reply_to: "mydruhhh@gmail.com"
            },
            isModalOpen: false,
            videoUrl: 'https://www.w3schools.com/html/mov_bbb.mp4',
            fullText: "Islamic Business Consultants", // Текст, который будет строиться по одной букве
            displayedText: "", // Показанный текст
            currentCharIndex: 0, // Индекс текущего символа
        };
    },
    components: {
        // Footer,
        // WelcomeSection,
        AboutSection,
        TeamSection,
        ModalVideo,
        SocialMediaspButton
    },
    mounted(){
        this.animateText()
    },
    methods: {
        openModal() {
            this.isModalOpen = true;
        },
        async sendMail(){
            try {
                if(this.mailForm.name == ''){
                    // alert('Укажите как к вам обращаться')
                    return
                }else if(this.mailForm.phone_number == '' || this.mailForm.phone_number == '+7'){
                    // alert('Укажите контактный номер телефона')
                    return
                }else if(this.mailForm.question == ''){
                    // alert('Укажите тему обращения')
                    return
                }

                console.log('sending mail')

                const response = await emailjs.send(
                'service_jiwnova',
                'template_1dvmey1',
                this.mailForm,
                'iGflyyHqPApeRmgXr'
                );

                if (response.status === 200) {
                    this.$toast('Обращение успешно отправлено!', {
                        'theme': 'auto',
                        'type': 'success'
                    })
                } else {
                    this.$toast('Произошла ошибка!', {
                        'theme': 'auto',
                        'type': 'error'
                    })
                }
            } catch (error) {
                this.$toast('Ошибка сервиса! ' + error, {
                    'theme': 'auto',
                    'type': 'error'
                })
            }
        },
        animateText() {
            if (this.currentCharIndex < this.fullText.length) {
                // Добавляем следующую букву к отображаемому тексту
                this.displayedText += this.fullText[this.currentCharIndex];
                this.currentCharIndex++;
                
                // Запускаем анимацию для следующей буквы с задержкой
                setTimeout(this.animateText, 100); // Задержка 100 мс между буквами
            }
        }
    },
  }
  </script>

  <style>

  </style>
  